/**
 * SankeyNode module
 */

/**
 * ============================================================================
 * IMPORTS
 * ============================================================================
 * @hidden
 */
import { Container, IContainerProperties, IContainerAdapters, IContainerEvents } from "../../core/Container";
import { SpriteEventDispatcher, AMEvent } from "../../core/Sprite";
import { SankeyDiagram, SankeyDiagramDataItem } from "../types/SankeyDiagram";
import { List } from "../../core/utils/List";
import { LabelBullet } from "./LabelBullet";
import { registry } from "../../core/Registry";
import { Color } from "../../core/utils/Color";
import { InterfaceColorSet } from "../../core/utils/InterfaceColorSet";
import * as $iter from "../../core/utils/Iterator";
import * as $string from "../../core/utils/String";
import * as $order from "../../core/utils/Order";
import * as $number from "../../core/utils/Number";

/**
 * ============================================================================
 * REQUISITES
 * ============================================================================
 * @hidden
 */

/**
 * Defines properties for [[SankeyNode]].
 */
export interface ISankeyNodeProperties extends IContainerProperties {

	/**
	 * Name of the node.
	 *
	 * @type {string}
	 */
	name?: string;

	/**
	 * A level node is at. (0 - ...)
	 *
	 * @type {number}
	 */
	level?: number;

	/**
	 * Node's numeric value.
	 *
	 * @type {number}
	 */
	value?: number;

	/**
	 * Node's color.
	 *
	 * @type {Color}
	 */
	color?: Color;

}

/**
 * Defines events for [[SankeyNode]].
 */
export interface ISankeyNodeEvents extends IContainerEvents { }

/**
 * Defines adapters for [[SankeyNode]].
 *
 * @see {@link Adapter}
 */
export interface ISankeyNodeAdapters extends IContainerAdapters, ISankeyNodeProperties { }


/**
 * ============================================================================
 * MAIN CLASS
 * ============================================================================
 * @hidden
 */

/**
 * Creates a node in a Sankey Diagram.
 *
 * A Sankey node is a block with a value, which represents its size on the
 * diagram.
 *
 * Nodes are connected via [[SankeyLink]] elements.
 *
 * @see {@link ISankeyNodeEvents} for a list of available events
 * @see {@link ISankeyNodeAdapters} for a list of available Adapters
 * @important
 */
export class SankeyNode extends Container {

	/**
	 * Defines available properties.
	 *
	 * @ignore Exclude from docs
	 * @type {ISankeyNodeProperties}
	 */
	public _properties!: ISankeyNodeProperties;

	/**
	 * Defines available adapters.
	 *
	 * @ignore Exclude from docs
	 * @type {ISankeyNodeAdapters}
	 */
	public _adapter!: ISankeyNodeAdapters;

	/**
	 * Defines available events.
	 *
	 * @type {ISankeyNodeEvents}
	 * @ignore Exclude from docs
	 */
	public _events!: ISankeyNodeEvents;

	/**
	 * A list of data items of the items coming in from another node, one level
	 * up.
	 *
	 * These are what ingoing links are build out of.
	 *
	 * @type {List<SankeyDiagramDataItem>}
	 */
	protected _incomingDataItems: List<SankeyDiagramDataItem>;

	/**
	 * A list of data items of the items going out of the node.
	 *
	 * These are what outgoing links are build out of.
	 *
	 * @type {List<SankeyDiagramDataItem>}
	 */
	protected _outgoingDataItems: List<SankeyDiagramDataItem>;

	/**
	 * Sorted list of incoming items.
	 *
	 * @type {Iterator<SankeyDiagramDataItem>}
	 */
	protected _incomingSorted: $iter.Iterator<SankeyDiagramDataItem>;

	/**
	 * Sorted list of outgoing items.
	 * @type {Iterator<SankeyDiagramDataItem>}
	 */
	protected _outgoingSorted: $iter.Iterator<SankeyDiagramDataItem>;

	/**
	 * [nextInCoord description]
	 *
	 * @ignore Exclude from docs
	 * @todo Description
	 * @type {number}
	 */
	public nextInCoord: number = 0;

	/**
	 * [nextOutCoord description]
	 *
	 * @ignore Exclude from docs
	 * @todo Description
	 * @type {number}
	 */
	public nextOutCoord: number = 0;

	/**
	 * A chart instance this node is added to.
	 *
	 * @type {SankeyDiagram}
	 */
	public chart: SankeyDiagram;

	/**
	 * A label element which shows node's value.
	 *
	 * @type {LabelBullet}
	 */
	public valueLabel: LabelBullet;

	/**
	 * A label element which shows node's name.
	 *
	 * @type {LabelBullet}
	 */
	public nameLabel: LabelBullet;

	/**
	 * Defines the type of the [[SankeyDiagramDataItem]] used in the class.
	 *
	 * @ignore Exclude from docs
	 * @type {SankeyDiagramDataItem}
	 */
	public _dataItem: SankeyDiagramDataItem;

	/**
	 * Constructor
	 */
	constructor() {
		super();
		this.className = "SankeyNode";

		this.width = 10;
		this.height = 10;

		this.isMeasured = false;

		let interfaceColors = new InterfaceColorSet();

		this.background.fill = interfaceColors.getFor("alternativeBackground");
		this.background.fillOpacity = 1;

		this.pixelPerfect = false;
		this.background.pixelPerfect = false;

		this.draggable = true;
		this.inert = true;

		this.events.on("positionchanged", this.invalidateLinks, this);
		this.events.on("sizechanged", this.invalidateLinks, this);

		let nameLabel = this.createChild(LabelBullet);
		nameLabel.shouldClone = false;
		//@should we auto update these locations if position is changed?
		nameLabel.locationX = 1;
		nameLabel.locationY = 0.5;
		nameLabel.label.text = "{name}";

		//nameLabel.label.textElement.hideOversized = false;
		nameLabel.width = 150;
		nameLabel.height = 150;
		nameLabel.label.horizontalCenter = "left";
		nameLabel.label.padding(0,5,0,5);
		this.nameLabel = nameLabel;


		let valueLabel = this.createChild(LabelBullet);
		valueLabel.shouldClone = false;
		valueLabel.label.hideOversized = false;
		valueLabel.locationX = 0.5;
		valueLabel.locationY = 0.5;
		valueLabel.width = 150;
		valueLabel.height = 150;
		//valueLabel.label.text = "{value}";
		valueLabel.label.horizontalCenter = "middle";
		this.valueLabel = valueLabel;

	}

	/**
	 * Marks node as invalid, for redrawal in the next update cycle.
	 *
	 * @ignore Exclude from docs
	 */
	public invalidate(): void {
		super.invalidate();
		this.invalidateLinks();
	}

	/**
	 * Invalidates all links, attached to this node.
	 *
	 * @ignore Exclude from docs
	 */
	public invalidateLinks(): void {
		this.nextInCoord = 0;
		this.nextOutCoord = 0;
		let chart = this.chart;

		if (chart) {
			let orientation = chart.orientation;
			if (this._incomingSorted) {
				$iter.each(this._incomingSorted, (dataItem) => {
					let link = dataItem.link;
					link.parent = this.chart.linksContainer;

					let x: number;
					let y: number;
					let angle: number;

					if (orientation == "horizontal") {
						x = this.pixelX + this.dx;
						y = this.nextInCoord + this.pixelY + this.dy;
						angle = 0;
					}
					else {
						y = this.pixelY + this.dy;
						x = this.nextInCoord + this.pixelX + this.dx;
						angle = 90;
					}

					link.endX = x;
					link.endY = y;
					link.startAngle = angle;
					link.endAngle = angle;
					link.gradient.rotation = angle;

					link.linkWidth = dataItem.value * chart.valueHeight;

					if (!dataItem.fromNode) {

						if (orientation == "horizontal") {
							link.startX = this.pixelX + this.dx - link.maxWidth;
							link.startY = link.endY;
						}
						else {
							link.startX = link.endX;
							link.startY = this.pixelY + this.dy - link.maxHeight;
						}

						let stop = link.gradient.stops.getIndex(0);
						if (stop) {
							if (link.colorMode == "gradient") {
								stop.color = this.color;
							}
							stop.opacity = 0;
							link.fill = link.gradient;
							link.gradient.validate();
						}
					}

					if (link.colorMode == "gradient") {
						let stop = link.gradient.stops.getIndex(1);
						if (stop) {
							stop.color = this.color;
							link.gradient.validate();
						}
					}

					this.nextInCoord += link.linkWidth;

					link.validate();
				});
			}

			if (this._outgoingSorted) {
				$iter.each(this._outgoingSorted, (dataItem) => {
					let link = dataItem.link;
					link.parent = this.chart.linksContainer;

					let x: number;
					let y: number;
					let angle: number;

					if (orientation == "horizontal") {
						angle = 0;
						x = this.pixelX + this.pixelWidth + this.dx - 1;
						y = this.nextOutCoord + this.pixelY + this.dy;
					}
					else {
						angle = 90;
						x = this.nextOutCoord + this.pixelX + this.dx;
						y = this.pixelY + this.pixelHeight + this.dy - 1;
					}

					link.startX = x;
					link.startY = y;
					link.startAngle = angle;
					link.endAngle = angle;
					link.gradient.rotation = angle;

					link.linkWidth = dataItem.value * this.chart.valueHeight;

					if (!dataItem.toNode) {
						if (orientation == "horizontal") {
							link.endX = this.pixelX + link.maxWidth + this.dx;
							link.endY = link.startY;
						}
						else {
							link.endX = link.startX;
							link.endY = this.pixelY + link.maxHeight + this.dy;
						}

						link.opacity = this.opacity;

						let stop = link.gradient.stops.getIndex(1);
						if (stop) {
							if (link.colorMode == "gradient") {
								stop.color = this.color;
							}
							stop.opacity = 0;
							link.fill = link.gradient;
							link.gradient.validate();
						}
					}

					if (link.colorMode == "gradient") {
						let stop = link.gradient.stops.getIndex(0);
						if (stop) {
							stop.color = this.color;
							link.gradient.validate();
						}
					}

					this.nextOutCoord += link.linkWidth;

					link.validate();
				});
			}
		}

		this.positionBullet(this.nameLabel);
		this.positionBullet(this.valueLabel);
	}

	/**
	 * Positions the bullet so it is centered within the node element.
	 *
	 * @param {LabelBullet}  bullet  Target bullet
	 */
	protected positionBullet(bullet: LabelBullet) {
		if (bullet) {
			bullet.x = this.pixelWidth * bullet.locationX;
			bullet.y = this.pixelHeight * bullet.locationY;
		}
	}

	/**
	 * List of incoming items (links).
	 *
	 * @readonly
	 * @return {List<SankeyDiagramDataItem>} Incoming items
	 */
	public get incomingDataItems(): List<SankeyDiagramDataItem> {
		if (!this._incomingDataItems) {
			let incomingDataItems = new List<SankeyDiagramDataItem>();
			incomingDataItems.events.on("insert", () => {
				if (this.chart.sortBy == "name") {
					this._incomingSorted = $iter.sort(this._incomingDataItems.iterator(), (x, y) => $string.order(x.fromName, y.fromName));
				}
				else if (this.chart.sortBy == "value") {
					this._incomingSorted = $iter.sort(this._incomingDataItems.iterator(), (x, y) => $order.reverse($number.order(x.value, y.value)));
				}
				else {
					this._incomingSorted = this._incomingDataItems.iterator();
				}
			}, this);
			this._incomingDataItems = incomingDataItems;
		}
		return this._incomingDataItems;
	}

	/**
	 * List of outgoing items (links).
	 *
	 * @readonly
	 * @return {List<SankeyDiagramDataItem>} Outgoing items
	 */
	public get outgoingDataItems(): List<SankeyDiagramDataItem> {
		if (!this._outgoingDataItems) {
			let outgoingDataItems = new List<SankeyDiagramDataItem>();
			outgoingDataItems.events.on("insert", () => {
				if (this.chart.sortBy == "name") {
					this._outgoingSorted = $iter.sort(this._outgoingDataItems.iterator(), (x, y) => $string.order(x.fromName, y.fromName));
				}
				else if (this.chart.sortBy == "value") {
					this._outgoingSorted = $iter.sort(this._outgoingDataItems.iterator(), (x, y) => $order.reverse($number.order(x.value, y.value)));
				}
				else {
					this._outgoingSorted = this._outgoingDataItems.iterator();
				}

			}, this);
			this._outgoingDataItems = outgoingDataItems;
		}
		return this._outgoingDataItems;
	}

	/**
	 * A name of the node.
	 *
	 * @param {string}  value  Name
	 */
	public set name(value: string) {
		this.setPropertyValue("name", value, true);
	}

	/**
	 * @return {string} Name
	 */
	public get name(): string {
		return this.getPropertyValue("name");
	}

	/**
	 * A level node is displayed at. (0 - ...)
	 *
	 * Levels are measured from left to right.
	 *
	 * The nodes in the left-most column will have `level = 0`.
	 *
	 * Nodes in second column - `level = 1`, etc.
	 *
	 * @param {number}  value  Level
	 */
	public set level(value: number) {
		this.setPropertyValue("level", value, true);
	}

	/**
	 * @return {number} Level
	 */
	public get level(): number {
		return this.getPropertyValue("level");
	}

	/**
	 * Node's numeric value.
	 *
	 * @param {number}  value  Value
	 */
	public set value(value: number) {
		this.setPropertyValue("value", value, true);
	}

	/**
	 * @return {number} Value
	 */
	public get value(): number {
		return this.getPropertyValue("value");
	}

	/**
	 * Node's color.
	 *
	 * @param {Color}  value  Color
	 */
	public set color(value: Color) {
		this.setPropertyValue("color", value, true);
		this.background.fill = value;
	}

	/**
	 * @return {Color} Color
	 */
	public get color(): Color {
		return this.getPropertyValue("color");
	}

	/**
	 * Copies properties and labels from another [[SankeyNode]].
	 *
	 * @param {SankeyNode}  source  Source node
	 */
	public copyFrom(source: this): void {
		super.copyFrom(source);
		this.nameLabel.copyFrom(source.nameLabel);
		this.valueLabel.copyFrom(source.valueLabel);
	}

}

/**
 * Register class in system, so that it can be instantiated using its name from
 * anywhere.
 *
 * @ignore
 */
registry.registeredClasses["SankeyNode"] = SankeyNode;
